}

table th {
    color: #6f51c7;
}
.slick-prev{
    position: absolute;
    left: -8px;
    top: 50%;
    transform: translateY(-50%);
    z-index: 2;
    font-size: 0;
    padding: 0;
    border: 0;
    width: 42px;
    height: 42px;
    border-radius: 0px 4px 4px 0px;
    background-color: #8184a1;
    outline: none;
    background-image: url('../img/svg/slick-prev.svg');
    background-repeat: no-repeat;
    background-size: 19px 26px;
    background-position: center;
    transition: all 200ms ease;
    &:before{
        width: 0;
        height: 0;
        content: "";
        position: absolute;
        left: 0;
        top: -4px;
        border-bottom: 4px solid #575b71;
        border-left: 8px solid transparent;

    }
    &:after{
        width: 0px;
        height: 0px;
        content: "";
        position: absolute;
        left: 0;
        bottom: -4px;
        border-left: 8px solid transparent;
        border-top: 4px solid #575b71;
    }
    &:hover{
        background-color: #fde037;
        &:before{
            border-bottom: 4px solid #bea201;
        }
        &:after{
            border-top: 4px solid #bea201;
        }
    }
}
.slick-next{
    position: absolute;
    right: -8px;
    top: 50%;
    transform: translateY(-50%);
    z-index: 2;
    font-size: 0;
    padding: 0;
    border: 0;
    width: 42px;
    height: 42px;
    border-radius: 4px 0px 0px 4px;
    background-color: #8184a1;
    outline: none;
    background-image: url('../img/svg/slick-next.svg');
    background-repeat: no-repeat;
    background-size: 19px 26px;
    background-position: center;
    transition: all 200ms ease;
    &:before{
        width: 0;
        height: 0;
        content: "";
        position: absolute;
        right: 0;
        top: -4px;
        border-bottom: 4px solid #575b71;
        border-right: 8px solid transparent;

    }
    &:after{
        width: 0px;
        height: 0px;
        content: "";
        position: absolute;
        right: 0;
        bottom: -4px;
        border-top: 4px solid #575b71;
        border-right: 8px solid transparent;
    }
    &:hover{
        background-color: #fde037;
        &:before{
            border-bottom: 4px solid #bea201;
        }
        &:after{
            border-top: 4px solid #bea201;
        }
    }
}
#mymodal{
    width: 300px;
    height: 200px;
    background: rgba(0,0,0,.8);
    padding: 20px;
    position: fixed;
    top: 50%;
    left: 50%;
    margin: -100px 0 0 -150px;
    display: none;
    z-index: 500;
    border-radius: 8px;
	span{
		color: #FFF;
		font-size: 30px;
		text-align: center;
		display: block;
		display: flex;
		align-items: center;
		height: 100%;
		font-family: "PT Sans";
		font-weight: 400;
	}
}
p{
    padding-bottom: 15px;
}
h1{
    padding: 10px 0px;
    font-size: 24px;
    font-weight: 600;
    color: #575b71;
    line-height: 1.1;
}
h3{
    font-size: 18px;
    font-weight: 600;
    color: #575b71;
    margin-bottom: 10px;
}
.tabs__caption {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 29px;
    li{
        font-size: 15px;
        font-weight: 700;
        letter-spacing: 1.8px;
        text-align: center;
        color: #000;
        border-bottom: 1px solid #000;
        margin-right: 35px;
        &:last-child{
            margin-right: 0;
        }
        &:not(.active){
            cursor: pointer;
            font-weight: 400;
            border-bottom: 1px solid transparent;
            &:hover{
                color: red;
            }
        }
    }
    &.active{
        
    }
}
.tabs__content {
    height: 0;
    overflow-y: hidden; 
    &.active{
        height: 100%;
        overflow-y: visible; 
    }
}
.noUi-target{
    border: 0;
    box-shadow: none;
    border-radius: 0;
}
.noUi-horizontal{
    height: 7px;
    .noUi-handle{
        width: 20px;
        height: 20px;
        box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.36);
        background: #13a4ff;
        border-radius: 2px;
        border: 0;
        &:before, &:after{
            display: none;
        }
    }
    .noUi-handle-lower{
        background-image: url('../img/svg/slick-prev.svg');
        background-repeat: no-repeat;
        background-position: center;
        background-size: 6px 17px;
    }
    .noUi-handle-upper{
        background-image: url('../img/svg/slick-next.svg');
        background-repeat: no-repeat;
        background-position: center;
        background-size: 6px 17px;
    }
}
.noUi-connect{
    background: #13a4ff;
}
.noUi-connects{
    background: #a0daff;
    border-radius: 0;
}