	align-items: center;
	&__item{
		margin-right: 25px;
		&:last-child{
			margin-right: 0;
		}
	}
	&__text{
		font-size: 12px;
    	color: #8184a1;
    	display: flex;
    	align-items: center;
    	&:first-child{
