		font-size: 14px;
		color: #8184a1;
		border: 1px solid #fff;
		border-radius: 7px;
		background: #fff;
	}
	&__btn{
		width: 62px;
		height: 29px;
		position: absolute;
		right: 9px;
		top: 9px;
		margin: 0px;
		padding: 0px;
		font-family: "Open Sans", sans-serif;
		font-size: 11px;
		color: #8184a1;
		text-transform: uppercase;
		border: 1px solid #dee0ee;
		border-radius: 4px;
		background: none;
