	}
}
.category{
	margin-bottom: 20px;
}
.category-list{
	display: flex;
	flex-wrap: wrap;
	&__item{
		width: calc(50% - 2px);
		position: relative;
		margin: 0 1px 2px;
		overflow: hidden;
		transition: all 300ms ease;
	}
	&__item--small{
		width: calc(25% - 2px);
	}
	&__content{
		position: absolute;
		bottom: 0;
		padding: 24px 31px;
		background: linear-gradient(to bottom, transparent, rgba(0, 0, 0, 0.5)); 
		width: 100%;
		transition: all 300ms ease;
	}
	&__image{
		height: 100%;
		display: block;
		overflow: hidden;
		position: relative;
		&:before {
			content: '';
			display: block;
			position: absolute;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
		    background: linear-gradient(to bottom, transparent, rgba(0, 0, 0, 0.5)); 
		    opacity: 0;
		    visibility: hidden;
			transition: all 300ms ease;
		}
		img{
			display: block;
			width: 100%;
			height: 100%;
		}
	}
	&__title{
		font-size: 24px;
	    font-weight: 600;
	    color: #fff;
	    line-height: 24px;
	}
	&__link{
		position: absolute;
		bottom: -31px;
		left: 31px;
		opacity: 0;
		border-radius: 4px;
    	background: #fde037;
		display: inline-block;
		padding: 0 27px 0 17px;
		height: 31px;
		line-height: 31px;
		text-align: center;
		text-transform: uppercase;
		transition: all 300ms ease;
		&:hover{
    		background: #575b71;
    		a{
				color: #fff;
				&:before{
			    	border-left: 5px solid #FFF;
				}
    		}
		}
		a{
			font-size: 11px;
			font-weight: 400;
			color: #402a1d;
			position: relative;
			&:before {
			    content: "";
			    border-top: 4px solid transparent;
			    border-bottom: 4px solid transparent;
			    border-left: 5px solid #bea201;
			    position: absolute;
			    right: -10px;
			    top: 50%;
			    transform: translateY(-50%);
				transition: all 300ms ease;
			}
		}
	}
	&__item:hover &{
		&__image{
			transform: scale(1.25);
		    background: linear-gradient(to bottom, transparent, rgba(0, 0, 0, 0.5));
		    &:before{
		    	opacity: 1;
		    	visibility: visible;
		    }
		}
		&__link{
			bottom: 20px;
			opacity: 1;
		}
		&__content{
			padding-bottom: 60px;
		}
	}
	&__item--small &{
		&__title{
			font-size: 18px;
    		line-height: 18px;
		}
	}
	&__item--small:hover &{
		&__content{
			padding-bottom: 24px;
		}
	}
}
