	padding: 7px 34px 7px 14px;
	margin-bottom: 5px;
	cursor: pointer;
	&:hover{
		border: 2px solid #fde037;
		background: transparent;
	}
}
.filter-content{
	background-color: #edeef8;
	border-radius: 4px;
	padding: 20px;
	display: none;
}
.filter-content-list{
	&__block{
		flex-grow: 1;
		transition: all 300ms ease;
		&:first-child{
			width: 134px;
			flex: none;
			text-align: right;
			margin-right: 20px;
		}
		&.is-hide{
			opacity: 0;
			visibility: hidden;
			transform: translateX(-10%);
		}
	}
	&__item{
		display: flex;
		align-items: center;
		border-bottom: 1px solid #dee0ee;
		padding: 12px 0;
		label{
			display: block;
			cursor: pointer;
			input[type="checkbox"]{
				display: none;
				&:checked + .label-text {
					background-color: #fde037;
					color: #000;
					b{
						background: #43484d;
					}
				}
			}
			.label-text{
				border-radius: 4px;
				padding: 0 12px;
				line-height: 30px;
				display: block;
				b{
					height: 15px;
				    display: inline-block;
				    margin-left: 7px;
				    padding: 0px 5px;
				    font-size: 10px;
				    color: #fff;
				    line-height: 15px;
				    border-radius: 4px;
				    background: #a0a4bc;
				}
			}
		}
	}
	&__text{
		display: inline-flex;
		align-items: center;
		font-size: 14px;
		font-weight: 700;
		color: #575b71;
		border-bottom: 1px dashed;
		cursor: pointer;
		&:hover{
			border-bottom: 1px dashed transparent;
			color: #cc9900;
			i{
				color: #cc9900;
			}
		}
		i{
			font-size: 18px;
    		color: #a0a4bc;
    		flex-grow: 1;
    		transition: all 200ms ease;
    		display: block;
    		margin-left: 7px;
    		transition: all 300ms ease;
		}
		&.is-active{
			i{
				transform: rotate(180deg);
			}
		}
	}
}
.range-slider{
	display: flex;
	align-items: center;
	&__slider{
		width: 192px;
		margin: 0 30px 0 20px;
	}
	&__input{
		font-family: "Open Sans", sans-serif;
		font-size: 13px;
		font-weight: 400;
		color: #8184a1;
		text-align: center;
		line-height: 22px;
		border: 1px solid #dee0ee;
		border-radius: 3px;
		background: #fff;
		width: 87px;
    	height: 28px;
	}
	&__text{
		color: #575b71;
    	line-height: 24px;
    	margin-right: 3px;
    	display: block;
    	&:last-child{
    		margin-right: 0;
    		margin-left: 5px;
    	}
	}
}
.filter-checkbox{
	display: flex;
	&__item{
		margin-right: 5px;
		&:last-child{
			margin-right: 0;
		}
	}
}
.filter-content-info{
	display: flex;
	justify-content: space-between;
}